@import "../core/Variables.scss";

.menu {
  background: linear-gradient(to right, lightblue 30%, lightyellow 100%);
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  &__paper {
    box-shadow: 5px 5px 5px rgba(6, 97, 128, 0.7);
    font-family: $mainText;
    word-spacing: 5px;
    background-color: rgba(white, 0.7);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 30px 0 30px;
    margin-bottom: 100px;
    gap: 25px;
    @media all and (min-width: 768px) {
      flex-direction: row;
      flex-wrap: wrap;
      font-size: 20px;
    width: 700px;

    }
    @media all and (min-width: 1200px) {
      flex-direction: row;
      flex-wrap: wrap;
      width: 900px;
      font-size: 25px;
    }
    &--div1 {
      margin-top: 25px;
      background-color: lightblue;
      box-shadow: 5px 5px 5px rgba(6, 97, 128, 0.7);
      
    }
    &--div2 {
      margin: 0 25px;
      background-color: lightblue;
      box-shadow: 5px 5px 5px rgba(6, 97, 128, 0.7);
      @media all and (min-width:768px){
        margin:25px 0 0 0;
      }
    }
    &--div3 {
      margin-bottom: 25px;
      background-color: white;
      box-shadow: 5px 5px 5px rgba(6, 97, 128, 0.7);
      
    }
  }
  &__back {
    margin-top: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    &--link {
      &:hover {
        -webkit-text-stroke: 2px rgb(6, 97, 128);
        color: rgb(129, 180, 197);
      }
      cursor: pointer;
      text-transform: uppercase;
      text-decoration: none;
      font-family: $mainText;
      color: rgb(6, 97, 128);
      font-size: 25px;
      @media all and (min-width: 768px) {
        font-size: 30px;
      }
      @media all and (min-width: 768px) {
        font-size: 35px;
      }
    }
  }
}

.aboutme {
  width: 220px;
  height: 165px;
  padding: 10px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  :hover {
    cursor: pointer;
    // scale: 1;
    // z-index: 4;
    // opacity: 1;
  }
  @media all and (min-width: 768px) {
    width: 300px;
    height: 245px;
  }
  @media all and (min-width: 1200px) {
    width: 400px;
    height: 345px;
  }
  &__bg {
    z-index: 1;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    &--text1 {
      z-index: 1;
      text-transform: uppercase;
      color: lightblue;
      -webkit-text-stroke: 1px black;
    }
    &--text2 {
      z-index: 0;
      position: absolute;
      transform: translate(-5%, 10%);
      text-transform: uppercase;
      color: lightyellow;
      -webkit-text-stroke: 1px black;
    }
  }
  &__img {
    width: 200px;
    opacity: 0.75;
    border-radius: 20px;
    @media all and (min-width: 768px) {
      width: 280px;
    }
    @media all and (min-width: 1200px) {
      width: 360px;
    }
  }
}

.projects {
  width: 220px;
  height: 165px;
  padding: 10px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  :hover {
    cursor: pointer;
    // scale: 1.1;
    // z-index: 4;
    // opacity: 1;
  }
  @media all and (min-width: 768px) {
    width: 300px;
    height: 245px;
  }
  @media all and (min-width: 1200px) {
    width: 400px;
    height: 345px;
  }
  &__bg {
    position: absolute;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    &--text1 {
      transform: translateX(60%);
      z-index: 1;
      text-transform: uppercase;
      color: lightblue;
      cursor: pointer;
      -webkit-text-stroke: 1px black;
    }
    &--text2 {
      z-index: 0;
      position: absolute;
      transform: translate(60%, -50%);
      text-transform: uppercase;
      color: lightyellow;
      cursor: pointer;
      -webkit-text-stroke: 1px black;
    }
    &--text3 {
      z-index: 2;
      position: absolute;
      transform: translateX(60%);
      text-transform: uppercase;
      color: white;
      cursor: pointer;
      -webkit-text-stroke: 1px black;
    }
  }

  &__img {
    width: 200px;
    opacity: 0.75;
    border-radius: 20px;
    @media all and (min-width: 768px) {
      width: 280px;
    }
    @media all and (min-width: 1200px) {
      width: 360px;
    }
  }
}

.contact {
  width: 220px;
  height: 70px;
  padding: 10px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  @media all and (min-width: 768px) {
    width: 640px;
  }
  @media all and (min-width: 1200px) {
    width: 840px;
    height: 100px;
  }
  &__bg {
    z-index: 1;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    :hover {
      // scale: 1.2;
      // z-index: 4;
    }
    &--text1 {
      z-index: 1;
      text-transform: uppercase;
      color: white;
      -webkit-text-stroke: 1px black;
      transform: translateY(30%);
    }
  }
  &__img {
    transform: translateY(-10%);
    width: 70px;
    border-radius: 20px;
    @media all and (min-width: 1200px) {
      width: 110px;
    }
  }
}
